<template>

  <div class="img_box">
    <!-- 头部导航栏 -->
  <div class="imageCSS">
    <!--登陆界面-->
  <div class="ms-login" v-show="!logState" >
        <div class="ms-title">东大网络拓扑结构<!--登陆标题--></div>
        <!-- <el-form class="ms-content" :model="info" @keyup.enter.native=submitUP()>   -->
        <el-form class="ms-content" :model="info" @keyup.enter.native=submitUP() >
          <el-form-item>
              <el-input v-model="info.username" >
                  <el-button  slot="prepend" :disabled="true">测试IP</el-button>
                  <!-- <el-button slot="append" :disabled="true">@seu.edu.cn</el-button> -->

              </el-input>
          </el-form-item>

          <el-form-item>
              <el-input type="textarea" :rows="6" show-password v-model="info.password" >
                  <el-button slot="prepend" :disabled="true">TraceRt结果</el-button>
              </el-input>
          </el-form-item>

          <div class="login-btn">
              <el-button type="primary" @click="submitUP()" >更新</el-button>
          </div>

          <div class="login-btn">
              <el-button type="primary" @click="showTopo()" >显示当前拓扑图</el-button>
          </div>
         
        </el-form>
    </div>
  </div>

  </div>

</template>


<script>
import axios from 'axios';
// import md5 from 'js-md5';
 export default({

    data: function(){
    return {
      activeIndex:"Login",
      useRouter:true,
      logState:false,
      info:{
          username:'',
          password:'',
          ip:'',
      },
      windowHeight:0,
      windowWidth:0,
    };
    },

   methods:{
     turnToResetPassword(){
       this.$router.push("/Resetpassword")
     },
     showTopo(){
        window.location.href = 'https://seusupermarket.cn/groupsPics/Seu_Topology.png';
     },
      submitUP(){
            console.log(this.info.username);
            console.log(this.info.password);
            var Info={
                "ipAddress":this.info.username,
                "Tracert":this.info.password,
            }
            axios
                 .post(process.env.URL_PATH+"/UpdateSEUTopology",Info)
                 .then((res)=>{
                     if(res.data['status']=="True")
                        {
                           this.$message.success("更新成功");
                        }else{
                            this.$message.error("发生错误");
                        }
                      this.info.password="";
                      this.info.username="";
                 })
                 .catch((err)=>{
                     this.$message.error("服务器已关闭", err);
                     })   

         },
         turnToRegister(){
             this.$router.push("/Register");
         },
         _isMobile() {
            // let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
            let flag = navigator.userAgent.match(/(phone|pod|iPhone|iPod|ios|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
            return flag;
          }

   },
   created(){
   },

   mounted() {
          this.logState=sessionStorage.getItem('logState');
          if(sessionStorage.getItem("windowHeight")==null){
             this.windowHeight=700;
          }else{
            this.windowHeight=parseInt(sessionStorage.getItem("windowHeight"));
          }

          var that = this;
        window.onresize = () => {
            return (() => {
              window.fullHeight = document.documentElement.clientHeight;
              window.fullWidth = document.documentElement.clientWidth;
              that.windowHeight = window.fullHeight;  // 高
              that.windowWidth = window.fullWidth; // 宽
            })()
          };

          window.addEventListener('beforeunload',()=>{
          sessionStorage.setItem("windowHeight",this.windowHeight);
        });


    }

 })

</script>

