<template>
  <div class="img_box">
    <!-- 头部导航栏 -->
 
  <el-menu :default-active="activeIndex" :router=true class="el-menu-demo" mode="horizontal">
    <el-menu-item index="/">Welcome</el-menu-item>
    <el-menu-item index="/AllGoods" >所有商品</el-menu-item>
    <!-- <el-menu-item index="/DataAnalysis" >数据分析</el-menu-item> -->
    <el-menu-item index="/Register" v-show="!logState" style="float:right;">注册</el-menu-item>
    <el-menu-item index="/User" v-show="logState" style="float:right;" >我的</el-menu-item>

    <el-submenu index="/AboutUs" style="float:right;" >
    <template v-slot:title>关于</template>
     <el-menu-item index="/UpdateHistory">版本公告</el-menu-item>
    <el-menu-item index="/Register/Terms">服务条款</el-menu-item>
    <el-menu-item index="/Contactus">关于我们</el-menu-item>
     <el-menu-item index="/DataAnalysis">数据分析</el-menu-item>
    </el-submenu>
  </el-menu>

<br>
    <div >
    <span style="text-align: center;display:block;overflow: hidden;white-space:nowrap;">网站数据统计</span>
    </div>
  <div clsss="chartsContainer">
    <div id="main" class="chartsCss"></div>
  </div>


  </div>

</template>


<script>
import axios from 'axios';
// import md5 from 'js-md5';
// 引入基本模板
let echarts = require('echarts/lib/echarts');
// 引入柱状图组件
require('echarts/lib/chart/bar');
//引入折线图
require('echarts/lib/chart/line');
// 引入提示框和title组件
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
require("echarts/lib/component/legend");
 export default({

    data: function(){
    return {
        currentTable:'',
        logState:false,
        name: 'Echarts',
        goodsHeight:"100%",
        userBaseInfoHeight:"0%",
        websiteHeight:"0%",
        webDataHeight:"0%",
        webDataHead:[],
        webData:[],
        webDate:[],
        newUser:[],
        newGoods:[],
        loginTimes:[],
        deleteGoods:[],
        saleGoods:[],
        saleMoney:[],
        tableName:[
            {
            value:'1',
            label:'商品信息'
            },
            {
            value:'2',
            label:'用户信息'
            },
            {
            value:'3',
            label:'kv信息'
            },
            {
            value:'4',
            label:'网站信息'
            },
        ],

      activeIndex:"/DataAnalysis", 
    };
    },

   methods:{

  changeSelectTable(){
          },
  updateCharts(){

  },

  getWebData(){

    axios
        .post(process.env.URL_PATH+"/Backend/GetWebData")
        .then((res)=>{
            //console.log(res);
            //console.log(res.data['status']);
            if(res.data['status']=="True")
            {
              // var tempHead=res.data['webDataColumn'];
              // console.log(tempHead0);
              // this.webDataHead=[];
              // for(var i=0;i<tempHead.length;i++){
              //   this.webDataHead.push(tempHead[i][0]);
              //   //this.webData[i]=[];
              // }

              
              var tempWebData=res.data['webData'];
              this.webDate=[];
              for(var i=0;i<tempWebData.length;i++){
                 this.webDate.push(tempWebData[i][0].split(' ')[2]+'/'+tempWebData[i][0].split(' ')[1]);
                 this.newUser.push(tempWebData[i][1]);
                this.newGoods.push(tempWebData[i][2]);
                
                this.loginTimes.push(parseInt(tempWebData[i][3]));
                this.deleteGoods.push(tempWebData[i][4]);
                this.saleGoods.push(tempWebData[i][5]);
                this.saleMoney.push(tempWebData[i][6]);
              }
              // console.log("********");
              
              var myChart = echarts.init(document.getElementById('main'));
             var option={
              title : {
                    // text: '网站数据',
                    //subtext: '纯属虚构'
                },
                tooltip : {
                    trigger: 'axis'
                },
                legend: {
                    data:['loginTimes','newGoods','saleGoods']
                },
                toolbox: {
                    show : true,
                    feature : {
                        mark : {show: true},
                        dataView : {show: true, readOnly: false},
                        magicType : {show: true, type: ['line', 'bar']},
                        restore : {show: true},
                        saveAsImage : {show: true}
                    }
                },
                calculable : true,
                
                xAxis : [
                    {
                        type : 'category',
                        name:'日期',
                        boundaryGap : false,
                        data :this.webDate,          
                        axisLabel:{interval: 0,rotate:40},

                    }
                ],
                yAxis : [
                    {
                        type : 'value',
                        name:"数量",
                        axisLabel : {
                            formatter: '{value}'
                        }
                    }
                ],
                series : [
                    {
                        name:'loginTimes',
                        type:'line',
                        data:this.loginTimes
                    },
                    {
                        name:'newGoods',
                        type:'line',
                        data:this.newGoods
                    },
                    {
                        name:'saleGoods',
                        type:'line',
                        data:this.saleGoods
                    },
              ]}

            myChart.setOption(option);   
              
            }else if(res.data['status']=="False"){
                this.$message.error("服务器查询失败");
            }else{
                this.$message.error("服务器错误");
            }
        })
        .catch((err)=>{
            this.$message.error("服务器已关闭",err);
            })   
  },
   },

   mounted() {
     this.getWebData();
      this.logState=sessionStorage.getItem('logState');
    }
     
 })

</script>

<style scoped>

.img_box{
 overflow: auto;
	width: 100%;height: 100%;
  /* background-color:rgba(220,220,220,0.4);	 */
}
.chartsCss{
  /* width: 100px;
  height:400px; */
  position: fixed;
  width: 100%;
  height: 80%;
  /* left:5%; */
  overflow: auto;
}

.chartsContainer{
    position: fixed;
  width: 100%;
  height: 80%;
   overflow: auto;
}

</style>

