<!-- 
template:UI
script:数据与操作
style:样式表，美化
margin:  上右下左边距
-->
<template>

  <div class="img_box" ref="con" id='cof' >
    <!-- 头部导航栏 -->
<el-backtop target=".img_box">
  <div style="{height: 100%; width: 100%; background-color: #f2f5f6; box-shadow: 0 0 6px rgba(0,0,0, .12);text-align: center; line-height: 40px; color: #1989fa;}">
     UP
    </div>
</el-backtop>
  <el-menu :default-active="activeIndex" :router=true class="el-menu-demo" mode="horizontal">
    <el-menu-item index="/">Welcome</el-menu-item>
    <el-menu-item index="/AllGoods" >所有商品</el-menu-item>

    <el-menu-item index="/Register" v-show="!logState" style="float:right;">注册</el-menu-item>
    <el-menu-item index="/User" v-show="logState" style="float:right;" >我的</el-menu-item>

    <el-submenu index="/AboutUs" style="float:right;" >
    <template slot="title">关于</template>
     <el-menu-item index="/UpdateHistory">版本公告</el-menu-item>
    <el-menu-item index="/Register/Terms">服务条款</el-menu-item>
    <el-menu-item index="/Contactus">关于我们</el-menu-item>
    <el-menu-item index="/DataAnalysis" >数据分析</el-menu-item>
    </el-submenu>
  </el-menu>
 <!-- 搜索和筛选-->
<br>
<el-row>
  <el-col :span="12">
    <el-input v-model="searchText" clearable @clear="handleClearSearch"
     placeholder="搜索物品"
  @keyup.enter.native="searchGoods()"
              style="width:60%;left:40%;"></el-input>
  </el-col>
  <el-col :span="2">
    <el-button @click="searchGoods()"
              style="background-color:rgba(148,0,211,1);font-size:15px; color:rgba(255,255,255,1)">搜索</el-button>
  </el-col>
  <!-- <el-col :span="1">
    <label>搜索</label>
  </el-col> -->
    <el-col :span="6">
    <el-select v-model="searchGoodsCampusText" clearable placeholder="按校区筛选"
    style="height:100%;" @change="changeSelectCampus">
                <el-option
                  v-for="item in campusArray"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
  </el-col>
  
</el-row>
 
  <br><br><br><br>
  <!-- 商品信息 -->

       <!-- currentPage  totalPage 页数等于 Math.ceil(finalShowPicNum/24) 
         实际下标是 currentPage*24+index+row*4 -->
      <el-row  :gutter="20" class="elRowStyle" v-for="(it, row) in currentPage+1<totalPage?6:(Math.ceil((finalShowPicNum-currentPage*24)/4))" :key="it" >
        <el-col :span="6" v-for="(o, index) in (currentPage*24+(row+1)*4)>finalShowPicNum?(finalShowPicNum-(row*4)-currentPage*24):4" :key="o" >
          <el-card @click.native="showGoodsInfo(currentPage*24+index+row*4)" class="cardStyle" :body-style="{ padding: '0px' }" shadow="hover">
            <img :src="finalShowPicSrc[currentPage*24+index+row*4]" class="image">
         
            <div>
              <span style="text-align: center;display:block;overflow: hidden;white-space:nowrap;">{{finalShowGoodsName[currentPage*24+index+row*4]}}</span>
              
              <span>{{"￥"+finalShowGoodsPrice[currentPage*24+index+row*4]}}</span>
              <span>{{"     "+finalShowGoodsCampus[currentPage*24+index+row*4]}}</span>
            </div>
          </el-card>
        </el-col>
      </el-row>
    <!-- </div> -->
    <el-row style="left:60%;" v-show="false">
      <el-button-group >
        <el-button type="primary" :disabled="lastDisabled" @click="showLastPage" icon="el-icon-arrow-left">上一页</el-button>
        <el-button type="primary" :disabled="nextDisabled" @click="showNextPage">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </el-row>
    <br>
    <el-row style="left:60%;height:100px;">
      <el-pagination
      layout="prev, pager, next"
      :total="totalPage*10"
      prev-text="上一页"
      next-text="下一页"
      background
      :current-page="currentPage+1"
      @current-change="handleChangePage">
      </el-pagination>
    </el-row>
  <br><br><br>
  </div>

</template>


<script>
import axios from 'axios';
 export default({

    data: function(){
    return {
        currentPage:0,
        totalPage:0,
        nextDisabled:false,
        lastDisabled:true,
        searchGoodsCampusText:'',
        searchText:'',
        useRouter:true,
        activeIndex: '/AllGoods',
        logState:false,
        userMail:'',
        picNum:0,
        searchPicNum:0,
        finalShowPicNum:0,

        //服务器里有的全部结果
        goodsName:[],
        goodsCampus:[],
        picSrc:[],
        picName:[],
        goodsPrice:[],
        goodsDes:[],
        goodsID:[],
        goodsContact:[],

        //搜索后的结果
        searchPicName:[],
        searchGoodsName:[],
        searchGoodsCampus:[],
        searchPicSrc:[],
        searchGoodsPrice:[],
        searchGoodsDes:[],
        searchGoodsID:[],
        searchGoodsContact:[],

        //最终展示出来的结果
        finalShowGoodsName:[],
        finalShowGoodsCampus:[],
        finalShowPicSrc:[],
        finalShowPicName:[],
        finalShowGoodsPrice:[],
        finalShowGoodsDes:[],
        finalShowGoodsID:[],
        finalShowGoodsContact:[],

        //各个校区
        campusArray: [
        {
          value:'全部校区',
          label:'全部校区'
        },
        {
          value: '九龙湖校区',
          label: '九龙湖校区'
        }, {
          value: '四牌楼校区',
          label: '四牌楼校区'
        }, {
          value: '丁家桥校区',
          label: '丁家桥校区'
        },
        {
          value: '苏州校区',
          label: '苏州校区'
        },
        {
          value: '无锡分校',
          label: '无锡分校'
        }],

    };
    },

   methods:{
     handleSelect(key, keyPath) {
      console.log(key, keyPath)
      },
      //清空搜索框
      handleClearSearch(){
        //this.$message.success("清空输入");
        this.searchGoodsName=this.goodsName;
        this.searchGoodsID=this.goodsID;
        this.searchGoodsPrice=this.goodsPrice;
        this.searchGoodsCampus=this.goodsCampus;
        this.searchGoodsDes=this.goodsDes;
        this.searchGoodsContact=this.goodsContact;
        this.searchPicSrc=this.picSrc;
        this.searchPicName=this.picName;
        this.searchPicNum=this.picNum;

        this.finalShowPicNum=this.picNum;
        this.finalShowGoodsName=this.goodsName;
        this.finalShowGoodsID=this.goodsID;
        this.finalShowGoodsPrice=this.goodsPrice;
        this.finalShowGoodsCampus=this.goodsCampus;
        this.finalShowGoodsDes=this.goodsDes;
        this.finalShowGoodsContact=this.goodsContact;
        this.finalShowPicSrc=this.picSrc;
        this.finalShowPicName=this.picName;

        this.searchText='';
        this.initPage();
      },
      //分页选择器
      handleChangePage(val){
        //this.$message.success("su"+val);
        this.currentPage=val-1;
        if(this.currentPage+1==this.totalPage){
          this.nextDisabled=true;
        }else {
          this.nextDisabled=false;
        }
        if(this.currentPage!=0){
          this.lastDisabled=false;
        }else{
          this.lastDisabled=true;
        }
         this.$refs.con.scrollTop = 0;
      },
      //新标签页显示商品详细信息
      showGoodsInfo(index){
        let routerJump=this.$router.resolve({path:'/AllGoods/GoodsDetail/'+this.finalShowGoodsID[index],
        query:{
          goodsID:this.finalShowGoodsID[index],
          goodsName:this.finalShowGoodsName[index],
          goodsPrice:this.finalShowGoodsPrice[index],
          goodsCampus:this.finalShowGoodsCampus[index],
          goodsDes:this.finalShowGoodsDes[index],
          goodsContact:this.finalShowGoodsContact[index],
          goodsPicUrl:this.finalShowPicName[index]
          }});
        //let routerJump=this.$router.resolve({name:'goodsDetail',params:{'goodsName':'test',id:this.searchGoodsID[index]}});
        window.open(routerJump.href);
      },
      //更新商品信息
      updateAllGoodsInfo(){
          var getUserGoodsInfo=sessionStorage.getItem("getUserGoodsInfo");
        if(getUserGoodsInfo==null){
            var userInfo={
                     "token":'',
                     
                 }
                this.goodsID=[],
                this.goodsContact=[],
                this.goodsName=[],
                this.goodsCampus=[],
                this.picSrc=[],
                this.goodsPrice=[],
                this.goodsDes=[],
                this.goodsID=[],
                this.goodsContact=[],
                this.picName=[],
                axios
                 .post(process.env.URL_PATH+"/User/getUserGoodsInfo",userInfo)
                 .then((res)=>{
                     if(res.data['status']=='true'){
                      //sessionStorage.setItem("getUserGoodsInfo",true);
                      this.picNum=parseInt(res.data['totalNum']);
                      
                      var info=res.data['userGoodsInfo'];

                      for(var i=0;i<info.length;i++){

                        this.goodsID.push(info[i][0]);
                        this.goodsName.push(info[i][1]);
                        this.goodsCampus.push(info[i][3]);
                        this.goodsPrice.push(info[i][4]);
                        this.goodsDes.push(info[i][5]);
                        this.goodsContact.push(info[i][6]);
                       
                        this.picSrc.push("https://seusupermarket.cn:443"+"/goodsPicFileSystem/ZIPpics/"+info[i][2])
                        this.picName.push(info[i][2]);
                       
                     }
                     
                     this.searchGoodsName=this.goodsName;
                     this.searchGoodsID=this.goodsID;
                     this.searchGoodsPrice=this.goodsPrice;
                     this.searchGoodsCampus=this.goodsCampus;
                     this.searchGoodsDes=this.goodsDes;
                     this.searchGoodsContact=this.goodsContact;
                     this.searchPicSrc=this.picSrc;
                     this.searchPicName=this.picName;
                     this.searchPicNum=this.picNum;
                      

                     this.finalShowGoodsName=this.goodsName;
                     this.finalShowGoodsID=this.goodsID;
                     this.finalShowGoodsPrice=this.goodsPrice;
                     this.finalShowGoodsCampus=this.goodsCampus;
                     this.finalShowGoodsDes=this.goodsDes;
                     this.finalShowGoodsContact=this.goodsContact;
                     this.finalShowPicSrc=this.picSrc;
                     this.finalShowPicName=this.picName;
                     this.finalShowPicNum=this.picNum;
                     this.totalPage=Math.ceil(this.finalShowPicNum/24);
                     } else{
                       this.$message.error("服务器异常");
                     } 
                 })
                 .catch((err)=>{
                     this.$message.error("服务器已关闭", err);
                     })   
    }

      },
      //下一页
      showNextPage(){
       
        if(this.currentPage+2==this.totalPage){
          this.currentPage++;
          this.nextDisabled=true;
          
        }else if(this.currentPage==this.totalPage-1){
          this.nextDisabled=true;
        }
        else{
          this.currentPage++;
        }
         if(this.currentPage!=0){
          this.lastDisabled=false;
        }
        //this.$message.success("下一页"+this.currentPage+" "+this.totalPage);
      },
      //上一页
      showLastPage(){
        if(this.currentPage==1){
          this.currentPage=0;
          this.lastDisabled=true;
        }else{
          this.currentPage--;
        }
        //this.$message.success("上一页"+this.currentPage+" "+this.totalPage);
        if(this.currentPage!=this.totalPage-1){
          this.nextDisabled=false;
        }
      },
      //搜索商品
      searchGoods(){
          this.searchGoodsCampusText='';
          if(this.searchText!='')
          {
            this.searchGoodsName=[];
            this.searchGoodsID=[];
            this.searchGoodsPrice=[];
            this.searchGoodsCampus=[];
            this.searchGoodsDes=[];
            this.searchGoodsContact=[];
            this.searchPicSrc=[];
            this.searchPicName=[];
            this.searchPicNum=0;
            //for(var i=0;i<this.picNum;i++){   
            for(var i = 0; this.goodsName[i]!=null; i++){
              if(this.goodsName[i].indexOf(this.searchText)!=-1){

                  this.searchGoodsName.push(this.goodsName[i]);
                  this.searchGoodsID.push(this.goodsID[i]);
                  this.searchGoodsPrice.push(this.goodsPrice[i]);
                  this.searchGoodsCampus.push(this.goodsCampus[i]);
                  this.searchGoodsDes.push(this.goodsDes[i]);
                  this.searchGoodsContact.push(this.goodsContact[i]);
                  this.searchPicSrc.push(this.picSrc[i]);
                  this.searchPicName.push(this.picName[i]);
                  this.searchPicNum++;
              }else{
                var likes=0;
                for(var j=0;j<this.searchText.length;j++)
                {
                  // console.log(this.goodsName[i][j]+"\n");
                  if(this.goodsName[i].indexOf(this.searchText[j])!=-1)
                    likes++;
                }
                likes=1.0*likes/this.searchText.length;
                // console.log(likes);
                if(likes>0.3)
                {
                  this.searchGoodsName.push(this.goodsName[i]);
                  this.searchGoodsID.push(this.goodsID[i]);
                  this.searchGoodsPrice.push(this.goodsPrice[i]);
                  this.searchGoodsCampus.push(this.goodsCampus[i]);
                  this.searchGoodsDes.push(this.goodsDes[i]);
                  this.searchGoodsContact.push(this.goodsContact[i]);
                  this.searchPicSrc.push(this.picSrc[i]);
                  this.searchPicName.push(this.picName[i]);
                  this.searchPicNum++;
                }
              }
            }
            this.finalShowGoodsName=this.searchGoodsName;
            this.finalShowGoodsID=this.searchGoodsID;
            this.finalShowGoodsPrice=this.searchGoodsPrice;
            this.finalShowGoodsCampus=this.searchGoodsCampus;
            this.finalShowGoodsDes=this.searchGoodsDes;
            this.finalShowGoodsContact=this.searchGoodsContact;
            this.finalShowPicSrc=this.searchPicSrc;
            this.finalShowPicName=this.searchPicName;
            this.finalShowPicNum=this.searchPicNum;
            this.initPage();
            if(this.finalShowPicNum==0){                    
              this.$message.warning("没有找到！可能是不存在该物品，也可能是搜索算法太捞！");
            }
            // this.initPage();
          }
          else{
            this.searchGoodsName=this.goodsName;
            this.searchGoodsID=this.goodsID;
            this.searchGoodsPrice=this.goodsPrice;
            this.searchGoodsCampus=this.goodsCampus;
            this.searchGoodsDes=this.goodsDes;
            this.searchGoodsContact=this.goodsContact;
            this.searchPicSrc=this.picSrc;
            this.searchPicName=this.picName;
            this.searchPicNum=this.picNum;

            this.finalShowPicNum=this.picNum;
            this.finalShowGoodsName=this.goodsName;
            this.finalShowGoodsID=this.goodsID;
            this.finalShowGoodsPrice=this.goodsPrice;
            this.finalShowGoodsCampus=this.goodsCampus;
            this.finalShowGoodsDes=this.goodsDes;
            this.finalShowGoodsContact=this.goodsContact;
            this.finalShowPicSrc=this.picSrc;
            this.finalShowPicName=this.picName;
            this.initPage();
          }
      },
      initPage(){
        this.currentPage=0;
        this.totalPage=Math.ceil(this.finalShowPicNum/24);
        if(this.totalPage==1){
          this.nextDisabled=true;
        }else{
          this.nextDisabled=false;
        }
        this.lastDisabled=true;
      },
      changeSelectCampus(value){
          this.searchByCampus(value);
        },
      //按校区筛选
      searchByCampus(cam){
        if(cam!=''&&cam!="全部校区")
        {
            this.finalShowGoodsName=[];
            this.finalShowGoodsID=[];
            this.finalShowGoodsPrice=[];
            this.finalShowGoodsCampus=[];
            this.finalShowGoodsDes=[];
            this.finalShowGoodsContact=[];
            this.finalShowPicSrc=[];
            this.finalShowPicName=[];
            this.finalShowPicNum=0;
            for(var i = 0; this.searchGoodsCampus[i]!=null; i++){
                  if(this.searchGoodsCampus[i]==cam){

                      this.finalShowGoodsName.push(this.searchGoodsName[i]);
                      this.finalShowGoodsID.push(this.searchGoodsID[i]);
                      this.finalShowGoodsPrice.push(this.searchGoodsPrice[i]);
                      this.finalShowGoodsCampus.push(this.searchGoodsCampus[i]);
                      this.finalShowGoodsDes.push(this.searchGoodsDes[i]);
                      this.finalShowGoodsContact.push(this.searchGoodsContact[i]);
                      this.finalShowPicSrc.push(this.searchPicSrc[i]);
                      this.finalShowPicName.push(this.searchPicName[i]);
                      this.finalShowPicNum++;
                  }
              }
            
        }else if(cam=="全部校区"){

          this.finalShowGoodsName=this.searchGoodsName;
          this.finalShowGoodsID=this.searchGoodsID;
          this.finalShowGoodsPrice=this.searchGoodsPrice;
          this.finalShowGoodsCampus=this.searchGoodsCampus;
          this.finalShowGoodsDes=this.searchGoodsDes;
          this.finalShowGoodsContact=this.searchGoodsContact;
          this.finalShowPicSrc=this.searchPicSrc;
          this.finalShowPicName=this.searchPicName;
          this.finalShowPicNum=this.searchPicNum;
        }else{

          this.finalShowGoodsName=this.searchGoodsName;
          this.finalShowGoodsID=this.searchGoodsID;
          this.finalShowGoodsPrice=this.searchGoodsPrice;
          this.finalShowGoodsCampus=this.searchGoodsCampus;
          this.finalShowGoodsDes=this.searchGoodsDes;
          this.finalShowGoodsContact=this.searchGoodsContact;
          this.finalShowPicSrc=this.searchPicSrc;
          this.finalShowPicName=this.searchPicName;
          this.finalShowPicNum=this.searchPicNum;
        }
        this.initPage();
        if(this.finalShowPicNum==0){
          this.$message.warning("没有找到！");
        }
      },
      
   },
   mounted() {
    this.logState=sessionStorage.getItem('logState');
    if(sessionStorage.getItem('logState')!='true'){
      this.$message.error('请先登录');
      sessionStorage.setItem("lastPage","AllGoods");
      this.$router.push("/");
    }else{
      this.postUrl=process.env.URL_PATH+'/Uploadfile/'+sessionStorage.getItem('userMail');
    }
    this.userMail=sessionStorage.getItem("userMail");
    this.updateAllGoodsInfo();
    }
     
 })

</script>

<style scoped>

.img_box{
	width: 100%;height: 100%;
  background-color:rgba(255,255,255,1);	
  overflow-x:hidden;
  overflow-y: auto;

}
.bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    /* height: 80px; */
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
.imgBack{
  position:absolute;
  
  top:18%;
  /* width: 100%; */
  right: 20%;
  max-height: 400px;
  left:20%;
  min-width: 400px;
  min-height: 500px;
  
  /* background-color:rgba(211,211,211,0.8); */
  background-color:rgba(255,255,255,0.8);
  overflow-x:hidden;
  overflow-y: auto;
}
.searchStyle{
  position:absolute;
  left:20%;
  top:10%;
  right: 20%;
}
.searchInputStyle{
  /* left: 10%; */
  width: 100%;
}
.searchButtonStyle{
  left: 60%;
  width: 10%;
}
.cardStyle{
    min-width: 100%;
    height: 100%; 
    /* margin-bottom: 20px; */
  max-width: 100px;

}

.elRowStyle{
  /* padding-bottom: 10px; */
  left:15%;
  right:15%;
  width:70%;
  margin-bottom: 20px;
  min-height: 120px;
  /* display: flex;
  flex-wrap: wrap; */

}

</style>

