<template>
  <div class="login-wrap"><!--整个界面-->
    <el-menu :default-active="activeIndex" :router=true class="el-menu-demo" mode="horizontal">
      <el-menu-item index="/">Welcome</el-menu-item>
      <el-menu-item index="/AllGoods" >所有商品</el-menu-item>
      <el-menu-item index="/Register" v-show="!logState" style="float:right;">注册</el-menu-item>
      <el-menu-item index="/User" v-show="logState" style="float:right;" >我的</el-menu-item>

      <el-submenu index="/AboutUs" style="float:right;" >
        <template v-slot:title>关于</template>
        <el-menu-item index="/UpdateHistory">版本公告</el-menu-item>
        <el-menu-item index="/Register/Terms">服务条款</el-menu-item>
        <el-menu-item index="/Contactus">关于我们</el-menu-item>
      </el-submenu>
    </el-menu>

    <div class="ms-login"><!--登陆界面-->
      <div class="ms-title">用户注册<!--登陆标题--></div>
      <el-form class="ms-content" :model="param" ref="param" :rules="rules" @keyup.enter=submitUP()>  
        <el-form-item prop="userMail">
          <label class="register-label">东 大 邮 箱：</label>
          <el-input class="register-inputMail" v-model="param.userMail">
          <template v-slot:append>@seu.edu.cn</template>
          </el-input>   
        </el-form-item>

        <el-form-item prop="name">
          <label class="register-label">用户名：</label>
          <!-- <el-input class="register-inputName" placeholder="请输入姓名" v-model="param.name"> -->
          <el-input class="register-inputName" v-model="param.name" maxlength="8">
          </el-input>
        </el-form-item>

        <el-form-item prop="password">
          <label class="register-label" >密码：</label>
          <el-input class="register-inputP1" type="password" show-password v-model="param.password">
          </el-input>
        </el-form-item>

        <el-form-item prop="password2">
          <label class="register-label">确认密码：</label>
          <el-input class="register-inputP2" type="password" show-password v-model="param.password2">
          </el-input>
        </el-form-item>

        <el-form-item prop="verifyCode">
          <label class="register-label">邮箱验证码：</label>
          <el-input class="register-input" v-model="param.verifyCode" maxlength="4"></el-input>
          <el-button type="primary" v-bind:disabled="dis" @click="submitGetCode()">{{param.vcodeText}}</el-button>
        </el-form-item>

        <el-form-item prop="checkedTerms">
          <el-checkbox v-model="param.checkedTerms" class="checkCss">阅读并同意
              <el-link href="https://seusupermarket.cn:443/Register/Terms" target="_blank" class="linkStyle">《服务条款》</el-link>
          </el-checkbox>
        </el-form-item>

        <div class="login-btn">
          <el-button type="primary" @click="submitUP()">注册</el-button>
        </div>
        <div>
          <label class="register-labelTip">邮箱即为登录账号</label>
          <br>
          <label class="register-labelTip">密码长度要求为8-16位，
                且只能包含大、小写字母<br>以及0-9之间的数字
          </label>
        </div>
      </el-form>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import md5 from 'js-md5';
 export default({
     data: function(){
        var checkPassword=(rule,value,callback)=>
        {
            let pattern = /^[a-zA-Z0-9]{8,20}$/;               
            if(!pattern.test(value))
            {
                callback(new Error('密码只能是大小写字母以及数字，且长度在8-20之间'));
            }
            callback();
        };
        return {
            
            activeIndex:"/Register",
            useRouter:true,
            logState:false,
            param:{
                userMail:'',
                name:'',
                password:'',
                password2:'',
                verifyCode:'',
                time:0,
                checkedTerms:false,
                vcodeText: '获取验证码',
            },
            dis:false,

            rules: {
                userMail: [
                    { required: true, message: '请输入邮箱', trigger: 'blur' },
                    // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                name: [
                    // { required: true, message: '请输入价格' },
                    //  { type: 'number', message: '价格必须为数字值'}
                    {required:true, message: '请输入用户名',trigger:'blur'},
                    { min: 4, max: 20, message: '长度在 4 到 20 个字符，只能是大小写字母和数字', trigger: 'blur' }
                ],
                password: [
                    // { required: true, message: '请输入价格' },
                    //  { type: 'number', message: '价格必须为数字值'}
                    {required:true,validator:checkPassword, message: '请输入正确格式的新密码',trigger:'change'},
                    { min: 8, max: 20, message: '长度在 8 到 20 个字符，只能是大小写字母和数字', trigger: 'blur' }
                ],
                password2:[
                    { required: true,validator:checkPassword, message: '请确认新密码',trigger: 'change' },
                     { min: 8, max: 20, message: '长度在 8 到 20 个字符，只能是大小写字母和数字', trigger: 'blur' }
                ],

                // verifyCode: [
                //     {  required: true,validator:checkVcode, message: '请输入验证码', trigger: 'blur'},
                //     //  { min: 4, max: 4, message: '长度为4', trigger: 'blur' }
                // ],

                
            },
            
        };
     },
     //******************************** */
     methods:{
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        submitGetCode(){
             this.$refs['param'].validate((valid) => {
                if (valid) 
                {
                    var mailInfo = {
                    "mailBox":this.param.userMail+'@seu.edu.cn',
                    };
                    if(this.param.userMail==sessionStorage.getItem("lastMail"))
                    {
                        this.param.time=60;
                        this.param.vcodeText='获取验证码('+this.param.time+')';
                        this.countTimeDown();
                        this.dis=true;
                        this.$message.warning("请查看您的邮箱，请勿重复操作！");  
                        return;

                    }
                    axios
                    .post(process.env.URL_PATH+"/Register/mail.json", mailInfo)
                    .then(
                        (res)=>{
                            sessionStorage.setItem("lastMail",this.param.userMail);
                            if(res.data['status']=="True")
                            {
                                this.$message.success("验证码已发送到您的东大电子邮箱！");  
                                this.param.time=60;
                                this.dis=true;
                                this.countTimeDown();
                            }
                            else if(res.data['status']=="False")
                            {
                                this.$message.error("用户已存在");  
                            }else if(res.data['status']=="vCodeExists"){
                                        this.$message.error("验证码已存在，请查看邮箱！");
                                        this.param.time=60;
                                        this.dis=true;
                                        this.countTimeDown();
                            }else
                            {
                                this.$message.error("服务器异常");  
                            }
                        })
                    .catch(
                        (err)=>{
                        console.log(err);
                        this.$message.error("服务器已关闭");
                        });  
                }else{
                this.$message.error("请输入正确格式的信息");
            }
         });           
         },
         countTimeDown(){
             if(this.param.time>0){
                 this.param.time--;
                 this.param.vcodeText='获取验证码('+this.param.time+')';
                 setTimeout(this.countTimeDown, 1000);
             }else{
                this.param.time = 0;
                this.param.vcodeText = '获取验证码';
                this.dis = false;
             }
         },
         submitUP(){
             this.$refs['param'].validate((valid) => {
                if (valid) 
                {
                    if(this.param.password==this.param.password2)
                        {
                            

                            let pattern = /^[0-9]{4,4}$/;               
                            if(!pattern.test(this.param.verifyCode))
                            {
                                this.$message.error("请输入正确格式的验证码");
                                return;
                            }
                            if(this.param.checkedTerms==false)
                            {
                                this.$message.warning("请阅读并同意服务条款");
                                return;
                            }
                            var tempPass=md5(this.param.password);
                            console.log(tempPass);
                            tempPass=tempPass.substr(0,16)+"piao"+tempPass.substr(16,16);
                            console.log(tempPass);
                            var userinfo = {
                                "mailBox":this.param.userMail+"@seu.edu.cn",
                                "token":md5(this.param.userMail+"@seu.edu.cn")+"piao",
                                "username": this.param.name,
                                "password": tempPass,
                                "vcode":this.param.verifyCode,
                                };
                            
                            axios
                            .post(process.env.URL_PATH+"/Register/register.json", userinfo)
                            .then((res)=>{
                                    if(res.data['status']=="True")
                                    {
                                        this.$message.success("注册成功！");        
                                        this.$router.push("/")   
                                    }else if(res.data['status']=="False"){
                                        this.$message.error("用户已存在");
                                    }else if(res.data['status']=="serverError"){
                                        this.$message.error("服务器错误");
                                    }else if(res.data['status']=="vcodeError"){
                                        this.$message.error("验证码不正确！");
                                    }else{
                                        this.$message.error("未知错误");
                                    }
                                })
                            .catch(
                                (err)=>{
                                console.log(err);
                                this.$message.error("服务器已关闭");
                                });
                        }
                        else
                        {
                            this.$message.error("两个密码不一致，请核对！");
                        }   
                }else{
                this.$message.error("请输入正确格式的信息");
            }
            //  if(this.param.userMail==''){
            //    this.$message.warning("请输入邮箱")
            //  } else if(this.param.name==''){
            //    this.$message.warning("请输入用户名")
            //  }else if(this.param.password==''){
            //    this.$message.warning("请输入密码")
            //  }else if(this.param.checkedTerms==false){
            //      this.$message.warning("请阅读并同意服务条款")
            //  }
            //  else
            //  {
            //     let pattern = /^[a-zA-Z0-9]{8,20}$/;
                
            //     if(!pattern.test(this.param.password))
            //     {
            //         this.$message.warning("密码不符合要求");
            //         this.param.password='';
            //     }else if(this.param.password2!=this.param.password)
            //     {
            //         this.$message.warning("请确认密码")
            //         this.param.password2='';
            //     }
            //     else if(this.param.verifyCode==''){
            //         this.$message.warning("请输入验证码")
            //     }
            //     else
            //     {
                    
            //     }
            // } 
         });
         }
     },
//      created(){
//         var that = this;
//         //当前页面监视键盘输入
//         document.onkeydown = function(e) {//事件对象兼容
//             // let e1 = e || event || window.event || arguments.callee.caller.arguments[0]
//             //键盘按键判断:左箭头-37;上箭头-38；右箭头-39;下箭头-40
//             //左
//             // if (e1 && e1.keyCode == 37) {
//             //     console.log("3");
//             // }

//             if(e.key=="Enter")
//             {
//               that.submitUP();
//             }
//         }
//    },
     mounted(){
         
        // this.param.time=parseInt(sessionStorage.getItem('timeLeft'));
        // if(this.param.time>0)
        // {
        //     this.param.vcodeText='获取验证码('+this.param.time+')';
        //     setTimeout(this.countTimeDown, 1000);
        //     this.dis=true;
        // }
        this.logState=sessionStorage.getItem("logState");
     }
 })

</script>

<style scoped>

