<script>
import axios from 'axios';
import md5 from 'js-md5';
export default({

  data: function(){
    const item = {
      date: '2016-05-02',
      name: '王小虎',
      address: '上海市普陀区金沙江路 1518 弄'
    };
    return {
      tableData: Array(80).fill(item),
      activeIndex:"/", 
      useRouter:true,
      logState:false,
      info:{
          // username:'213182328',
          // password:'88888888',
          username:'',
          password:'',
          ip:'',
      },
      windowHeight:0,
      windowWidth:0,
      link_font_size: 30,
      menu_height: 80,
      state: ({
        size: 1, //默认字体大小值
        val: "",//双向绑定的值
        //字体数组
        posts: [
          { id: 1, name: "28", size: 1 },
          { id: 2, name: "32", size: 2 },
          { id: 3, name: "10", size: 2 },
        ],
      })
    };
    },
    methods:{
     turnToResetPassword(){
       this.$router.push("/Resetpassword")
     },
      //选择字体事件
      changtext(val) {
        //如果选中的是10号字体就字体大小相减
        if (val == 3) {
          this.state.size = this.state.size -= 1;
        } else {
          this.state.size = this.state.size += 1;
        }
      },
      submitUP(){
             if(this.info.username=='')
             {
                 this.$message.warning("请输入账号");
             }else if(this.info.password=='')
             {
                 this.$message.warning("请输入密码");
             }else
             {  
               if(this.info.username=="root"&&this.info.password=="999110")
               {
                 sessionStorage.setItem("BackendState",true);
                 this.$router.push("/Backend");
               }else
               {
                //  var md5Password=md5(this.info.password)+'piao';
                 var md5Password=md5(this.info.password);
                 md5Password=md5Password.substr(0,16)+"piao"+md5Password.substr(16,16);
                 var token=md5(this.info.username+'@seu.edu.cn')+'piao';
                 var userInfo={
                     "username":this.info.username+"@seu.edu.cn",
                     "password":md5Password,
                     "token":token,
                 }
               
                 axios
                 .post(process.env.URL_PATH+"/Login/login.json",userInfo)
                 .then((res)=>{
                     if(res.data['status']=="True")
                        {
                            // this.$message.success("登录成功！"); 
                            this.$message.success({message:"登录成功！",duration:1000}); 
                            sessionStorage.setItem('logState', true); 
                            sessionStorage.setItem('userName', res.data['userInfo']['userName']);  
                            sessionStorage.setItem('userMail', this.info.username+"@seu.edu.cn");  
                            sessionStorage.setItem("onlineGoods",res.data['userInfo']['onlineGoods']);
                            sessionStorage.setItem("maxGoods",res.data['userInfo']['maxGoods']);
                            sessionStorage.setItem("userID",res.data['userInfo']['userID']);
                            // console.log(res.data['userInfo']['userID']);
                            sessionStorage.setItem("token",token);   
                            console.log("=============");
                            console.log('登录状态',sessionStorage.getItem('logState'));
                            console.log('登录用户名',sessionStorage.getItem('userMail'));
                            console.log('用户名',sessionStorage.getItem('userName'));
                            this.$router.push("/AllGoods");
                        }else if(res.data['status']=="False"){
                            this.$message.error("密码错误");
                        }else if(res.data['status']=="userNotExits"){
                            this.$message.error("用户不存在");
                        }else{
                            this.$message.error("服务器错误");
                        }
                 })
                 .catch((err)=>{
                     this.$message.error("服务器已关闭",err);
                     })   
               }
             }
         },
         turnToRegister(){
             this.$router.push("/Register");
         },
         _isMobile() {
            // let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
            let flag = navigator.userAgent.match(/(phone|pod|iPhone|iPod|ios|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
            return flag;
          }
         
    },
    created(){
        // var that = this;
        // //当前页面监视键盘输入
        // document.onkeydown = function(e) {//事件对象兼容
        //     // let e1 = e || event || window.event || arguments.callee.caller.arguments[0]
        //     //键盘按键判断:左箭头-37;上箭头-38；右箭头-39;下箭头-40
        //     //左
        //     // if (e1 && e1.keyCode == 37) {
        //     //     console.log("3");
        //     // }

        //     if(e.key=="Enter")
        //     {
        //       that.submitUP();
        //     }
        // }
    },

    mounted() {
     if(this._isMobile()==null){
       console.log('pc');
     }else{
       if(this._isMobile()[0]!='iOS')   
       {
         window.location.href = 'https://m.seusupermarket.cn';
       }
     }
          console.log(this._isMobile());
          
          sessionStorage.setItem("lastPage","/");
          this.logState=sessionStorage.getItem('logState');
          if(sessionStorage.getItem("windowHeight")==null){
             this.windowHeight=700;
          }else{
            this.windowHeight=parseInt(sessionStorage.getItem("windowHeight"));
          }

          var that = this;
          // that.$refs.elLogin.$el.children[0].focus();
          // that.$refs.buttLogin.$el.children[0].focus();

        // <!--把window.onresize事件挂在到mounted函数上-->
        window.onresize = () => {
            return (() => {
              window.fullHeight = document.documentElement.clientHeight;
              window.fullWidth = document.documentElement.clientWidth;
              that.windowHeight = window.fullHeight;  // 高
              that.windowWidth = window.fullWidth; // 宽
            })()
          };
          
          window.addEventListener('beforeunload',()=>{
          sessionStorage.setItem("windowHeight",this.windowHeight);
        });

        
    }
     
 })

