<!-- 
template:UI
script:数据与操作
style:样式表，美化
margin:  上右下左边距
-->
<template>

  <div class="img_box">
    <!-- 头部导航栏 -->
 
  <el-menu :default-active="activeIndex" :router=true class="el-menu-demo" mode="horizontal">
    <el-menu-item index="/">Welcome</el-menu-item>
    <el-menu-item index="/AllGoods" >所有商品</el-menu-item>
    <el-menu-item index="/Register" v-show="!logState" style="float:right;">注册</el-menu-item>
    <el-menu-item index="/User" v-show="logState" style="float:right;" >我的</el-menu-item>

    <el-submenu index="/AboutUs" style="float:right;" >
    <template v-slot:title>关于</template>
     <el-menu-item index="/UpdateHistory">版本公告</el-menu-item>
    <el-menu-item index="/Register/Terms">服务条款</el-menu-item>
    <el-menu-item index="/Contactus">关于我们</el-menu-item>
    </el-submenu>
  </el-menu>
  
    <el-row>
        <el-col :span="6">
            <el-select v-model="currentTable" clearable placeholder="筛选表"
            style="height:100%;" @change="changeSelectTable">
                <el-option
                v-for="item in tableName"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </el-col>
  
    </el-row>
    <el-table

        :data="tableGoodsInfo"
        
        :height="goodsHeight"
        border    fit    >
        <el-table-column 
        v-for="(value,index) in goodsInfoLength" :key="index"
        :sortable="index==0?true:false"
        :prop="index.toString()" :label="goodsInfoHead[index]" ></el-table-column>
    </el-table>

    <el-table

        :data="tableUserBaseInfo"
        style="width: 100%"
        :height="userBaseInfoHeight"
        border fit
        >
        <el-table-column 
        v-for="(value,index) in userBaseInfoLength" :key="index"
        :sortable="index==0?true:false"
        :prop="index.toString()" :label="userBaseInfoHead[index]" ></el-table-column>
    </el-table>

    <el-table

        :data="tableWebsiteInfo"
        style="width: 100%"
        :height="websiteHeight"
        border fit
        :default-sort = "{prop: '0', order: 'descending'}"
        >
        <el-table-column 
        v-for="(value,index) in websiteInfoLength" :key="index"
        :sortable="index==0?true:false"
        :prop="index.toString()" :label="websiteInfoHead[index]" ></el-table-column>
    </el-table>

        <el-table

        :data="tableWebData"
        style="width: 100%"
        :height="webDataHeight"
        border fit
        :default-sort = "{prop: '0', order: 'descending'}"
        >
        <el-table-column 
        v-for="(value,index) in webDataLength" :key="index"
        :sortable="index==0?true:false"
        :prop="index.toString()" :label="webDataHead[index]" ></el-table-column>
    </el-table>
 
  </div>

</template>


<script>
import axios from 'axios';
// import md5 from 'js-md5';
 export default({

    data: function(){
    return {
        goodsHeight:"100%",
        userBaseInfoHeight:"0%",
        websiteHeight:"0%",
        webDataHeight:"0%",
        
        tableName:[
            {
            value:'1',
            label:'商品信息'
            },
            {
            value:'2',
            label:'用户信息'
            },
            {
            value:'3',
            label:'kv信息'
            },
            {
            value:'4',
            label:'网站信息'
            },
        ],
        currentTable:'',
        // showGoods:true,
        // showUser:false,
        // showWeb:false,
      activeIndex:"Login", 
      useRouter:true,
      logState:false,
      tableGoodsInfo:[],
      goodsInfoHead:[
        "goodsID",
        "goodsName" ,
        "picID" ,
        "campus" ,
        "price" ,
        "description" ,
        "contact" ,
        "ownerID" ,
        "visable" ,
      ],
      goodsInfoLength:0,
     tableUserBaseInfo:[],
     userBaseInfoHead:[
        "goodsID",
        "goodsName" ,
        "picID" ,
        "campus" ,
        "price" ,
        "description" ,
        "contact" ,
        "ownerID" ,
        "visable" ,
      ],
      userBaseInfoLength:0,

      tableWebsiteInfo:[],
      websiteInfoHead:[],
      websiteInfoLength:0,

      tableWebData:[],
      webDataHead:[],
      webDataLength:0,

    };
    },

   methods:{
       changeSelectTable(value){
           this.currentTable=value;
           //console.log(this.currentTable);
           //console.log(value);
           if(this.currentTable=="1"){
               console.log("********");
               this.goodsHeight="100%";
               this.userBaseInfoHeight="0%";
               this.websiteHeight="0%";
               this.webDataHeight="0%";
            //    this.showGoods=true;
            //    this.showUser=false;
            //    this.showWeb=false;
           }else if(this.currentTable=="2"){
               this.goodsHeight="0%";
               this.userBaseInfoHeight="100%";
               this.websiteHeight="0%";
               this.webDataHeight="0%";
            //    this.showGoods=false;
            //    this.showUser=true;
            //    this.showWeb=false;
           }
           else if(this.currentTable=="3"){
               this.goodsHeight="0%";
               this.userBaseInfoHeight="0%";
               this.websiteHeight="100%";
               this.webDataHeight="0%";
            //    this.showGoods=false;
            //    this.showUser=false;
            //    this.showWeb=true;
           }else if(this.currentTable=="4"){
               this.goodsHeight="0%";
               this.userBaseInfoHeight="0%";
               this.websiteHeight="0%";
               this.webDataHeight="100%";
            //    this.showGoods=false;
            //    this.showUser=false;
            //    this.showWeb=true;
           }
       },
      getWebInfo(){    
        axios
        .post(process.env.URL_PATH+"/Backend/GetWebInfo")
        .then((res)=>{
            //console.log(res);
            if(res.data['status']=="True")
            {
                this.tableGoodsInfo=res.data['goodsInfo'];
                var tempGoodsInfo=res.data['goodsInfoColumn'];
                this.goodsInfoHead=[];
                this.goodsInfoLength=0;
                //console.log(tempGoodsInfo.length);
                for(var i=0;i<tempGoodsInfo.length;i++)
                {
                    //console.log(tempGoodsInfo[i][0]);
                    this.goodsInfoHead.push(tempGoodsInfo[i][0]);
                    this.goodsInfoLength++;
                }
                
                var tempUserBaseInfo=res.data['userBaseInfoColumn'];
                this.tableUserBaseInfo=res.data['userBaseInfo'];
                this.userBaseInfoHead=[];
                this.userBaseInfoLength=0;
                for(i=0;i<tempUserBaseInfo.length;i++)
                {
                    this.userBaseInfoHead.push(tempUserBaseInfo[i][0]);
                    this.userBaseInfoLength++;
                }

                var tempWebsiteInfo=res.data['websiteInfoColumn'];
                this.tableWebsiteInfo=res.data['websiteInfo'];
                this.websiteInfoHead=[];
                this.websiteInfoLength=0;
                for(i=0;i<tempWebsiteInfo.length;i++)
                {
                    this.websiteInfoHead.push(tempWebsiteInfo[i][0]);
                    this.websiteInfoLength++;
                }

                var tempWebData=res.data['webDataColumn'];
                this.tableWebData=res.data['webData'];
                this.webDataHead=[];
                this.webDataLength=0;
                for(i=0;i<tempWebData.length;i++)
                {
                    this.webDataHead.push(tempWebData[i][0]);
                    this.webDataLength++;
                }
                
            }else if(res.data['status']=="False"){
                this.$message.error("密码错误");
            }else if(res.data['status']=="userNotExits"){
                this.$message.error("用户不存在");
            }else{
                this.$message.error("服务器错误");
            }
        })
        .catch((err)=>{
            this.$message.error("服务器已关闭",err);
            })   
         },
         
   },

   mounted() {
        this.logState=sessionStorage.getItem('BackendState');
        if(sessionStorage.getItem('BackendState')!="true"){
            this.$message.error('请先登录');
            // sessionStorage.setItem("lastPage","AllGoods");
            this.$router.push("/");
        }
        else
        {
            this.getWebInfo();
        }

        
    }
     
 })

</script>

<style scoped>

.img_box{
  overflow: hidden;
	width: 100%;height: 100%;
  /* background-color:rgba(220,220,220,0.4);	 */

}

</style>

